/* ==================== common styles ======================= */
html {
  font-size: 10px;
  /*used for rem-units*/
  overflow-x: hidden;
}

body {
  position: relative;
  min-width: 320px;
  overflow-x: hidden;
  color: #1e1e1e;
  font-family: OpenSansRegular, sans-serif;
  font-size: 20px;
  padding-bottom: 300px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

button {
  border: none;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: -100%;
  background: rgba(53, 63, 104, 0.3);
  z-index: 8;
  transition: all .2s;
}

.overlay.active {
  right: 0;
}

.blur {
  filter: blur(4px);
  transition-duration: .2s;
}

.flex {
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  outline: none;
  border: none;
  max-width: 100%;
}

input,
input:focus {
  outline: none;
  border: none;
  background: none;
}

::-webkit-input-placeholder {
  font-family: OpenSansSemiBold, sans-serif;
  font-size: 18px;
  color: #969696;
}

::-moz-placeholder {
  font-family: OpenSansSemiBold, sans-serif;
  font-size: 18px;
  color: #969696;
}

:-ms-input-placeholder {
  font-family: OpenSansSemiBold, sans-serif;
  font-size: 18px;
  color: #969696;
}

::placeholder {
  font-family: OpenSansSemiBold, sans-serif;
  font-size: 18px;
  color: #969696;
}

section {
  position: relative;
  padding: 2rem 0;
  outline: 1px solid #FFFF62;
  transition: all 0s;
}

.wrapper {
  position: relative;
  width: 96%;
  max-width: 1110px;
  margin: 0 auto;
  outline: 1px solid blue;
}

h2 {
  font-family: GaramoundBold, serif;
  font-size: 72px;
}

strong {
  font-family: OpenSansSemiBold, sans-serif;
  color: #1e1e1e;
}

.heading {
  font-family: GaramoundBold, serif;
  font-size: 60px;
  line-height: 1.2;
  color: #515151;
  text-align: right;
}

p,
ul,
ol {
  line-height: 1.5;
}

ol {
  list-style-type: decimal;
  list-style-position: inside;
}

.button-cont {
  text-align: center;
  padding: 10px 0;
}

.button {
  display: inline-block;
  position: relative;
  height: 56px;
  min-width: 255px;
  border-radius: 28px;
  line-height: 36px;
  padding: 10px 5.7rem;
  font-family: OpenSansBold, sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  color: #353f68;
  text-align: center;
  background-color: #ebebeb;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(50, 50, 50, 0.5);
  transition: all 1.5s cubic-bezier(0.77, 0, 0.175, 1);
  cursor: pointer;
  z-index: 0;
}

.button.long {
  padding-left: 4rem;
  padding-right: 4rem;
}

.button.offset {
  left: 17%;
}

.button:before, .button:after {
  content: "";
  position: absolute;
  transition: all 0.6s cubic-bezier(0.77, 0, 0.175, 1);
  z-index: -1;
}

.button:before {
  top: 0;
  left: 50%;
  height: 100%;
  width: 0;
  border: 3px solid #353f68;
  border-left: 0;
  border-right: 0;
}

.button:after {
  bottom: 0;
  left: 0;
  height: 0;
  width: 100%;
  background: #353f68;
  transition-delay: .2s;
}

.button:hover {
  color: #fff;
}

.button:hover:before {
  transition-delay: 0;
  left: 0;
  width: 100%;
}

.button:hover:after {
  transition-delay: .4s;
  top: 0;
  height: 100%;
}

/*-------------------------- header --------------------------*/
.header {
  top: 0;
  left: 0;
  right: 0;
  background: white;
  padding: 18px 0 20px;
  box-shadow: 0 5px 20px rgba(100, 100, 100, 0.3);
  transition: all 0s;
  z-index: 1;
}

.header.sticky {
  padding: 6px 0;
  background: rgba(255, 255, 255, 0.9);
}

.header.sticky .logo-cont {
  max-width: 120px;
}

.header.sticky nav a {
  padding: 6px 0;
}

.header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-cont {
  display: block;
  margin-right: 20px;
  max-width: 300px;
  transition: all .4s;
}

nav a {
  display: inline-block;
  position: relative;
  font-family: OpenSansBold, sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  color: #000;
  padding: 20px 0;
  transition: all .4s;
}

nav a:after {
  content: "";
  display: block;
  width: 0;
  height: 4px;
  background-color: #353f68;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width .4s;
}

nav a:hover:after, nav a.active:after {
  width: 100%;
}

.header-nav ul,
.footer-nav ul {
  width: 100%;
  text-align: right;
  white-space: nowrap;
}

.header-nav li,
.footer-nav li {
  display: inline-block;
  margin-right: 9.2%;
}

.header-nav li:last-child,
.footer-nav li:last-child {
  margin-right: 0;
}

.header-nav {
  flex-grow: 1;
  padding-left: 35px;
  text-align: right;
}

.header-nav ul {
  display: inline-block;
  max-width: 740px;
}

/*-------------------------- mobile nav --------------------------*/
.btn-menu {
  display: none;
  position: fixed;
  top: 20px;
  right: 15px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  text-align: right;
  z-index: 2;
}

.btn-menu span {
  display: inline-block;
  background: #fff;
  position: absolute;
  top: 17px;
  left: 8px;
  right: 8px;
  width: 30px;
  height: 6px;
  border-radius: 3px;
  border: 2px solid #353f68;
}

.btn-menu span:before, .btn-menu span:after {
  content: "";
  position: absolute;
  display: inline-block;
  right: -2px;
  width: 40px;
  height: 6px;
  border-radius: 3px;
  background: #fff;
  border: 2px solid #353f68;
}

.btn-menu span:before {
  top: -12px;
}

.btn-menu span:after {
  top: 8px;
}

.mob-cont {
  position: fixed;
  width: 260px;
  background-color: #353f68;
  top: 0;
  bottom: 0;
  right: -280px;
  overflow-y: auto;
  padding: 20px 30px;
  box-shadow: -5px 0 20px 1px rgba(50, 50, 50, 0.9);
  z-index: 9;
  color: #fff;
  transition: all .2s 0s;
}

.mob-cont.active {
  right: 0;
  transition: all .2s .3s;
}

.mob-cont a {
  display: inline-block;
  color: #fff;
  padding: 10px 0;
  margin-bottom: 10px;
}

.mob-cont a:after {
  background: #fff;
}

.mob-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}

.mob-top .mob-logo-cont {
  display: block;
  max-width: 120px;
  background: #fff;
  padding: 5px;
}

.close {
  font-size: 36px;
  cursor: pointer;
}

/*-------------------------- slogan section  --------------------------*/
.slogan-section {
  text-align: right;
  padding: 0;
  min-height: 200px;
}

.slogan-section:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #353f68;
  z-index: -1;
}

.slogan-section .wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.main-img {
  position: relative;
  display: block;
  margin: 0 auto;
  z-index: -1;
}

.main-heading {
  position: absolute;
  width: 100%;
  top: 49%;
  transform: translateY(-50%);
  font-family: GaramoundBold, serif;
  font-size: 54px;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.8);
}

.main-heading span {
  display: inline-block;
}

.main-heading span:first-letter {
  color: #353f68;
}

/*-------------------------- text section --------------------------*/
.text-section {
  padding: 4rem 0 2rem;
}

.text-section p:last-child {
  margin-bottom: 0;
}

.text-section ul li:before {
  content: "-";
  margin-right: 4px;
}

.text-section p,
.text-content p {
