}

.index-text {
  padding: 8rem 0;
  font-size: 18px;
}

.index-text .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  overflow: hidden;
  outline: 1px solid transparent;
}

.index-text p {
  flex: 1 1 680px;
  line-height: 1.7;
  text-align: justify;
  margin-right: 20px;
}

.index-text p:last-of-type {
  margin-bottom: 0;
}

.logo-big {
  position: relative;
  flex: 2 1 140px;
  max-width: 255px;
}

.logo-big img {
  display: block;
  width: 100%;
}

.line {
  position: absolute;
  top: 0;
  right: 8.8%;
  width: 16%;
  height: 66%;
  background: #fff;
}

/*-------------------------- appoint form section --------------------------*/
.form-section {
  padding: 0;
  background-color: #353f68;
  background-image: url(../img/commonImgs/form-bg.jpg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: auto 100%;
}

.form-section .wrapper {
  display: flex;
  align-items: center;
  padding: 165px 0;
  max-height: 100vh;
}

.form-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.form-text {
  color: #fff;
  text-align: right;
  flex-grow: 1;
  padding-right: 25px;
}

.form-text h4 {
  font-family: GaramoundBold, serif;
  font-size: 40px;
  line-height: 1;
  letter-spacing: .5px;
}

.form-text p {
  font-size: 20px;
  line-height: 1.5;
}

.form-cont {
  flex-grow: 1;
  max-width: 540px;
  height: 56px;
  border-radius: 28px;
  background-color: #fff;
  overflow: hidden;
}

.appoint-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

input[type="tel"] {
  padding: 14px 28px 10px;
  font-size: 18px;
}

input[type="submit"] {
  display: block;
  height: 56px;
  min-width: 255px;
  border-radius: 28px;
  line-height: 36px;
  padding: 10px 42px;
  font-family: OpenSansSemiBold, sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  color: inherit;
  text-align: center;
  transition: all .4s;
  cursor: pointer;
  z-index: 0;
}

.submit-wrap {
  position: relative;
  height: 56px;
  border-radius: 28px;
  box-shadow: 0 0 20px rgba(50, 50, 50, 0.5);
  cursor: pointer;
  color: #353f68;
  background-color: #ebebeb;
  overflow: hidden;
  z-index: 0;
}

/*-------------------------- services section --------------------------*/
.index-services-section {
  padding: 6rem 0 3rem;
}

.index-services-section .wrapper {
  display: flex;
  justify-content: space-between;
}

.index-services-section h2 {
  position: absolute;
  top: 46%;
  transform: translateY(-50%);
  text-align: center;
}

.index-services-section h2,
.text-section h2 {
  width: 100%;
  color: #e8e8e8;
}

.index-services-section .righted,
.text-section .righted {
  color: #515151;
}

.index-services-section p + ul,
.index-services-section p + ol,
.text-section p + ul,
.text-section p + ol {
  margin-top: -3rem;
}

.left-side {
  position: relative;
  flex: 1 1 320px;
  background-image: url(../img/commonImgs/clip.png);
  background-repeat: no-repeat;
  background-position: 91% 81%;
}

.right-side {
  flex: 1 1 570px;
  padding-left: 63px;
}

.services-list li {
  display: flex;
  align-items: center;
  position: relative;
  min-height: 67px;
  line-height: 1.33;
  font-family: OpenSansBold, sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  color: #232323;
}

.services-list li:before {
  content: "";
  font-size: 0;
  display: inline-block;
  vertical-align: middle;
  width: 43px;
  min-width: 43px;
  height: 45px;
  margin-right: 34px !important;
  background-image: url(../img/sprite/servicesSprite.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.services-list a:hover {
  text-decoration: underline;
}

.protect:before {
  background-position: 0 -3px;
}

.international:before {
  background-position: 0 -67px;
}

.support:before {
  background-position: 0 -135px;
}

.consult:before {
  background-position: 0 -200px;
}

/*-------------------------- news section --------------------------*/
.news-section {
  padding: 2.5rem 0 6rem;
}

.news-section h2 {
  color: #e8e8e8;
  padding-right: 46px;
  flex: 0 1 350px;
  line-height: .7;
}

.head-cont {
  display: flex;
  margin-bottom: 4.5rem;
}

.head-cont:before {
  content: "";
  display: inline-block;
  background-color: #e8e8e8;
  flex: 1 1 100px;
  margin-right: 30px;
}

.load-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 -15px 5px;
}

.load-container:after {
  content: "";
  display: inline-block;
  height: 0;
  flex: 0 0 349px;
  margin: 0 15px;
}

.item {
  flex: 0 0 349px;
  margin: 0 15px 4.3rem;
}

.item-wrap,
.item-wrap-link {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 349px;
  height: 100%;
  margin: 0 auto;
  box-shadow: 0 0 20px rgba(50, 50, 50, 0.5);
  overflow: hidden;
}

.item-img-cont {
  flex: 0 0 250px;
  overflow: hidden;
}

.item-img-cont img {
  height: 250px;
  display: block;
  margin: 0 auto;
  transition: all .6s;
}

.offers-section {
  padding: 4rem 0;
}

.offers-section .item-img-cont {
  flex: 0 0 260px;
}

.offers-section .item-img-cont img {
  height: 260px;
}

.offers-section .prev-text {
  padding: 26px 15px 24px;
}

.offers-section .prev-text > div:first-child {
  margin-bottom: 26px;
}

.offers-section .title,
.offers-section .price,
.offers-section ul,
.offers-section ol {
  padding-left: 16px;
  padding-right: 16px;
}

.prev-text {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  align-content: space-between;
  max-width: 100%;
  position: relative;
  padding: 15px 20px;
  background: #fff;
  z-index: 0;
}

.prev-text > div {
  width: 100%;
}

.prev-text .title {
  font-family: GaramoundBold, serif;
  font-size: 22px;
  line-height: 1.2;
  text-transform: uppercase;
  color: #353f68;
  max-width: 1,00%;
  transition: all .6s;
}

.prev-text .price {
  font-family: OpenSansSemiBold, sans-serif;
  font-size: 25px;
  color: #353f68;
  margin-bottom: 23px;
}

.prev-text .date {
  font-size: 14px;
  color: #969696;
  margin-bottom: 12px;
  transition: all .6s;
}

.prev-text .more {
  display: inline-block;
  position: relative;
  padding-bottom: 3px;
  font-family: OpenSansSemiBold, sans-serif;
  font-size: 16px;
  color: #353f68;
  transition: all .6s;
}

.prev-text .more:before {
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  background: #fff;
  transition: all .6s;
}

.prev-text .more:after {
  content: ">>";
  margin-left: 10px;
  vertical-align: middle;
}

.prev-text .button {
  display: block;
  font-size: 16px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

.prev-text ul,
.prev-text ol {
  padding-top: 12px;
}

.prev-text p,
.prev-text li {
  font-size: 16px;
  line-height: 1.5;
  color: #353f68;
}

.prev-text li {
  max-width: 280px;
}

.prev-text li:before {
  content: "-";
  margin-right: 4px;
}

/*-------------------------- footer --------------------------*/
.footer {
  background: #000;
  color: #fff;
  padding: 25px 0;
  transition: all 0s;
}

.footer .wrapper {
  display: flex;
  justify-content: space-between;
}

.footer-left {
  min-width: 235px;
}

.social {
  margin-top: 15px;
}

.social li {
  display: inline-block;
  margin-right: 8px;
}

.social li:last-of-type {
  margin-right: 0;
}

.social a {
  display: inline-block;
  width: 45px;
  height: 45px;
  line-height: 45px;
  border-radius: 50%;
  background: #fff;
  filter: grayscale(100%);
  transition: all .8s;
  overflow: hidden;
}

.social a:hover {
  filter: none;
}

.social a:hover img {
  transform: scale(1.15);
}

.social a img {
  display: block;
  width: 100%;
  transition: all .8s;
}

.footer-right {
  flex-grow: 1;
  max-width: 740px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

.footer-nav {
  width: 100%;
}

.footer-nav a {
  color: #fff;
}

.copy {
  width: 100%;
  margin: 35px 0 0;
  font-size: 16px;
  text-align: right;
  line-height: 1;
}

/*-------------------------- footer end --------------------------*/
/*-------------------------- vertical decor lines --------------------------*/
.v-lines-cont,
.v-lines-cont .wrapper {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.v-lines-cont {
  z-index: -1;
}

.v-line {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 1px;
  background: rgba(200, 200, 200, 0.2);
}

.v-line:nth-child(1) {
  left: 0;
}

.v-line:nth-child(2) {
  left: 23%;
}

.v-line:nth-child(3) {
